export default {
	data() {
		return {
			user: {}
		};
	},
	onShow() {
		let user = uni.getStorageSync('user');
		this.user = user;
	},
	methods: {
		modifySex() {
			uni.navigateTo({
				url: '../gender/gender'
			});
		},
		modifyBirthday() {
			uni.navigateTo({
				url: '../birthday/birthday'
			});
		},
		modifyNickname() {
			uni.navigateTo({
				url: '../nickName/nickName'
			});
		},
		operator() {
			uni.showActionSheet({
				itemList: ['查看我的头像', '从手机相册中选择'],
				success: (res) => {
					let index = res.tapIndex
					if (index === 0) {
						// 预览头像
						let faceArr = []
						faceArr.push(this.user.faceImage)
						uni.previewImage({
							urls: faceArr,
							current: faceArr[0]
						})
					}
					if (index === 1) {
						uni.navigateTo({
							url:'../myFace/myFace'
						})
						uni.chooseImage({
							count: 1,
							sizeType:['compressed'],
							sourceType: ['album'],
							success: res => {
								let img = res.tempFilePaths[0]
								uni.navigateTo({
									url: '/pages/myFace/myFace?img=' + img
								})
							}
						})
					}
				}
			})
		},
		cleanStorage() {
			uni.clearStorageSync();
			uni.showToast({
				title: '清理缓存成功',
				icon: 'success'
			});
			setTimeout(() => {
				uni.switchTab({
					url: '/pages/me/me'
				});
			}, 1000);
		},
		logout() {
			uni.request({
				url: `${this.$api}/user/logout`,
				method: 'POST',
				data: {
					qq: '7929290',
					userId: this.user.id
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (res.data.status === 200) {
						uni.removeStorageSync('user');
						uni.showToast({
							title: '退出成功',
							icon: 'success'
						});
						setTimeout(() => {
							uni.switchTab({
								url: '/pages/me/me'
							});
						}, 1000);
					}
				}
			});
		}
	}
};
